*{
    box-sizing: border-box;
}
.formButton{
    background: #A5DEFF;
    border-radius: 4px;
    width: 66px;
    height: 34px;
    position: absolute;
    right: 5%;
    bottom: 9%;
}
.modalBlock{
    position: relative;
}
.formTitle{
    font-weight: 500;
    font-size: 20px;
    font-family: 'Source Code Pro', monospace;
    color: white;
    margin: 0;
    position: absolute;
    top: 8%;
    left:6%
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[name="minutes" i] {
    color: #985EFF;
    font-size: 30px;
    position: absolute;
    top: 25%;

    left: 41.5%;
    text-align: center;
    appearance: none;
}
input[name="seconds" i] {
    text-align: center;
    color: #985EFF;
    font-size: 30px;
    position: absolute;
    top: 25%;
    right:13%;
}
input[name="hours" i] {
    color: #985EFF;
    font-size: 30px;
    text-align: center;
    position: absolute;
    top: 25%;
    left: 13%;

}
.buttons{

    margin-bottom: 10px;
    display: flex;

}

.timer .buttons{
    position: absolute;
    bottom: 14%;
}
.buttons div{
    border-radius: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    background:#A5DEFF;
    text-align: center;
    width: 5rem;
    font-size: 1.5rem;
    margin-right: 4rem;
    padding: 1rem;
}

.buttons div:hover{
    transition-duration: .5s;
}
.buttons div:last-child{
    margin-right: 0;
}
.time{
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    display: flex;
}
.time div{
    border-radius: 10%;
    font-family: 'Source Code Pro', monospace;
    font-weight: lighter;
    font-size: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    width: 10rem;
    height: 10rem;
    margin-right: 100px;
    background: #080F1A;
}

.time div:last-child{
    margin-right: 0;
}
.timer{
    position: relative;
    box-shadow: 20px -15px 10px 5px rgba(0,0,0,.5);
    border-radius: 20% / 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80rem;
    height: 40rem;
    background: #092143CC;
    padding: 10px;
}
.title{
    font-size: 50px;
    font-weight: lighter;
    position: absolute;
    top:10%;
    letter-spacing: 30px;
    color: #FFF;
    font-family: 'Source Code Pro', monospace;
}
.modalButtons{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.modalButtons div{
    margin-top: 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    margin-right: 3rem;
    background: #985EFF;
    width: 20%;
    height: 6vh;
    border-radius: 4px;
}
.modalButtons div:last-child{
    margin-right: 0;
}
